*{
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
}

body{
    background-color: #00A4F5;
}

.all{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
}
.title{
    position: absolute;
    top: 100px;
    width: 100%;
    font-family: 'Courier New', Courier, monospace;
    text-align: center;
   
}


.flashcard{
    
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: antiquewhite;
    border-radius: 20px;
    width: 400px;
    height: 250px;
}


.flashcard .buttons{
    padding-top: 60px;
    display: flex;
    justify-content: space-around;
    width: 400px;
}

.flashcard .buttons button{
    background: none;
    display: flex;
    justify-content: center;
    align-items: center;
   border: none;
   border-radius: 20px;
   width: 70px;
   height: 20px;
   color: peru;
   font-size: 50px;
  
}